import logo from './logo.svg';
import React, {useState, useEffect} from "react";
import axios from "axios";
import './App.css';

function App() {
    let [liveEvents, setLiveEvents] = useState([])
    useEffect(() => {
        axios({
            method: "get",
            url: '/api/live_events/',
        }).then(response => {
            setLiveEvents(response.data);
        })
    }, [])
    console.log(liveEvents)



    return (
        <div className="App">
            <h1>hello</h1>
            <ul>
                {liveEvents.map(event => (
                    <li key={event.id}>{event.name}|{event.counters.map(c => (
                        <i>{c.value}</i>
                    ))}</li>
                ))}
            </ul>
        </div>
    );
}

export default App;
